<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="post_container">
                    <div class="post_c_t">
                        <span>编辑岗位</span>
                        <span class="r">
                            <span class='rs'>*</span>
                            <span>必填项</span>
                        </span>
                    </div>
                    <div class="from_data">
                        <el-row>
                            <el-col :span='12' :offset='3'>
                                <el-form ref="form" :model="form" label-width="100px" :rules='rules'>
                                    <el-form-item label="岗位类型：" class='is-required'>
                                        <el-select v-model="form.post_type" placeholder="请选择" style='width:100%;'>
                                            <el-option v-for="(item,index) in options" :key="index" :label="item.typeName" :value="item.id">
                                            </el-option>
                                        </el-select>
                                    </el-form-item>
                                    <el-form-item label="岗位名称：" class='is-required' prop='name'>
                                        <el-input v-model="form.name"></el-input>
                                    </el-form-item>
                                    <el-form-item label="岗位人数：" prop='num' class='is-required'>
                                        <el-input v-model="form.num"></el-input>
                                    </el-form-item>
                                    <el-form-item label="岗位职责：" class='is-required' prop='responsibility'>
                                        <el-input type="textarea" v-model="form.responsibility" :rows='10'></el-input>
                                    </el-form-item>
                                    <el-form-item label="任职要求：" class='is-required' prop='requirement'>
                                        <el-input type="textarea" v-model="form.requirement" :rows='10'></el-input>
                                    </el-form-item>
                                    <el-form-item>
                                        <el-button type="primary" @click='save("form")'>保存草稿</el-button>
                                        <el-button @click='sub("form")'>发布</el-button>
                                    </el-form-item>
                                </el-form>
                            </el-col>
                        </el-row>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        var checkAge = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('岗位人数不能为空'));
            }
            setTimeout(() => {
                if (value > 25) {
                    callback(new Error('岗位人数不能超过25人'));
                } else {
                    callback();
                }
            }, 1);
        };
        return {
            title: '编辑岗位',
            form: {
                post_type: '',
                name: '',
                num: '',
                responsibility: '',
                requirement: '',
                id: ''
            },
            options: [],
            rules: {
                num: [{
                    required: true,
                    message: '岗位人数不能为空',
                    trigger: 'blur'
                }, {
                    pattern: /^[0-9]*$/,
                    message: '只能输入数值'
                }],
                name: [{
                    required: true,
                    message: '岗位名称'
                }, {
                    min: 1,
                    max: 20,
                    message: '不能超过20字'
                }],
                responsibility: [{
                    required: true,
                    message: '岗位职责不能为空'
                }, {
                    min: 1,
                    max: 500,
                    message: '不能超过500字'
                }],
                requirement: [{
                    required: true,
                    message: '岗位要求不能为空'
                }, {
                    min: 1,
                    max: 500,
                    message: '不能超过500字'
                }],
                post_type: [{
                    required: true,
                    message: '岗位类型不能为空',
                    trigger: 'change'
                }]
            }
        }
    },
    methods: {
        /*保存*/
        save(formName) {
            var _this = this
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('确认保存, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then((action) => {
                        if (action === 'confirm') {
                            _this.$axios.post('/tomlive/talentRecruitment/updateByPrimaryKeyStatus', this.$qs.stringify({
                                jobTypeId: this.form.post_type,
                                jobName: this.form.name,
                                jobStatement: this.form.responsibility,
                                jobRequirement: this.form.requirement,
                                talentNumber: this.form.num,
                                id: this.form.id
                            }), {
                                headers: {

                                }
                            }).then((res) => {
                                console.log(res)
                                if (res.status == 200) {
                                    this.$router.push('/home/post_bc_c')
                                } else {
                                    alert('添加失败')
                                }
                            })

                        }
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '取消修改'
                        });
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
            /*
            this.$confirm('确认保存, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    _this.$axios.post('/tomlive/talentRecruitment/updateByPrimaryKeyStatus', this.$qs.stringify({
                        jobType: this.form.post_type,
                        jobName: this.form.name,
                        jobStatement: this.form.responsibility,
                        jobRequirement: this.form.requirement,
                        talentNumber: this.form.num,
                        id: this.form.id
                    }), {
                        headers: {

                        }
                    }).then((res) => {
                        console.log(res)
                        if (res.status == 200) {
                            this.$router.push('/home/post_bc_c')
                        } else {
                            alert('添加失败')
                        }
                    })

                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消修改'
                });
            })*/
        },
        /*发布*/
        sub(formName) {
            var _this = this
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // alert('submit!');
                    this.$confirm('确认添加岗位, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then((action) => {
                        if (action === 'confirm') {
                            _this.$axios.post('/tomlive/talentRecruitment/updateByPrimaryKeySelective', this.$qs.stringify({
                                jobTypeId: this.form.post_type,
                                jobName: this.form.name,
                                jobStatement: this.form.responsibility,
                                jobRequirement: this.form.requirement,
                                talentNumber: this.form.num,
                                id: this.form.id
                            }), {
                                headers: {

                                }
                            }).then((res) => {
                                console.log(res)
                                if (res.status == 200) {
                                    this.$router.push('/home/post_fb')
                                } else {
                                    alert('添加失败')
                                }
                            })
                        }
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '取消修改'
                        });
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
            /*this.$confirm('确认添加岗位, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    _this.$axios.post('/tomlive/talentRecruitment/updateByPrimaryKeySelective', this.$qs.stringify({
                        jobType: this.form.post_type,
                        jobName: this.form.name,
                        jobStatement: this.form.responsibility,
                        jobRequirement: this.form.requirement,
                        talentNumber: this.form.num,
                        id: this.form.id
                    }), {
                        headers: {

                        }
                    }).then((res) => {
                        console.log(res)
                        if (res.status == 200) {
                            this.$router.push('/home/post_fb')
                        } else {
                            alert('添加失败')
                        }
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消修改'
                });
            })*/
        }
    },
    mounted() {
        this.$axios.post('/tomlive/talentType/selectAllTalentType').then((res) => {
            /*var arr=res.data.info;
            var arr2=[];
            for(var i=0;i<arr.length;i++){
            	arr2.push(arr[i].jobType)
            }*/
            this.options = res.data.info
            console.log(res)
        })
        var arr2 = window.sessionStorage.getItem('post_x')
        var arr = JSON.parse(arr2)
        this.form.post_type = arr.typeId
        this.form.name = arr.jobName
        this.form.responsibility = arr.jobStatement
        this.form.requirement = arr.jobRequirement
        this.form.num = arr.talentNumber
        this.form.id = arr.id
    }
}
</script>
<style scoped>
.post_container {
    width: 100%;
    min-height: 500px;
    background: #fff;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    background: #fff;
    /* padding: 0 20px; */
    margin: 20px 0;
}

.post_c_t {
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    color: #666;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
    font-weight: bold;
}

.post_c_t>span {
    margin: 0 0 0 20px;
}

.from_data {
    width: 100%;
    margin: 50px 0;
}

.post_c_t>.r {
    float: right;
    margin: 0 20px 0 0;
}

.r>.rs {
    color: red;
    margin: 0 5px;
}
</style>
<style>
.el-textarea .el-textarea__inner {
    resize: none;
}
</style>